
.main-header {
  display: flex;
  padding: 0 16px;
  background-color: #FFF;
  // border-bottom: 1px solid #cecece;
  margin-bottom: 1px;
  div{
    h1 {
      display: inline;
      margin: 0 10px;
      color: #4b4b4b;
      vertical-align: top;
    }
  }
}
.main-logo {
  flex: 1;
